}
.btn-yellow{
  .button-variant(@btn-yellow-color, @btn-yellow-bg, @btn-yellow-border);
}
.btn-blue{
  .button-variant(@btn-blue-color, @btn-blue-bg, @btn-blue-border);
}
.btn-sky{
  .button-variant(@btn-sky-color, @btn-sky-bg, @btn-sky-border);
}
.btn-green{
