  }
  &.btn-danger {
    .border-button-variant(@btn-danger-color; @btn-danger-bg; @btn-danger-border);
  }
  &.btn-inverse {
    .border-button-variant(@btn-inverse-color; @btn-inverse-bg; @btn-inverse-border);
  }
  &.btn-orange {
