//
// Typography
// --------------------------------------------------


// Headings
// -------------------------

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
  font-family: @headings-font-family;
  font-weight: @headings-font-weight;
  line-height: @headings-line-height;
  color: @headings-color;

  small,
  .small {
    font-weight: normal;
    line-height: 1;
    color: @headings-small-color;
  }
}

h1, .h1,
h2, .h2,
h3, .h3 {
  margin-top: @line-height-computed;
  margin-bottom: (@line-height-computed / 2);

  small,
  .small {
    font-size: 65%;
  }
}
h4, .h4,
h5, .h5,
h6, .h6 {
  margin-top: (@line-height-computed / 2);
  margin-bottom: (@line-height-computed / 2);

  small,
  .small {
    font-size: 75%;
  }
}

h1, .h1 { font-size: @font-size-h1; }
h2, .h2 { font-size: @font-size-h2; }
h3, .h3 { font-size: @font-size-h3; }
h4, .h4 { font-size: @font-size-h4; }
h5, .h5 { font-size: @font-size-h5; }
h6, .h6 { font-size: @font-size-h6; }

// Giant
// --------------------------

.text-giant{ 
  font-size: @font-size-giant;
  line-height: (@line-height-computed *4);
  @media (max-width: @screen-xs-max) {
    font-size: 45px;
  }
}

// Remove text
// --------------------------

.text-remove{
  text-decoration:line-through;
}

// Body text
// -------------------------

p {
  margin: 0 0 (@line-height-computed / 2);
}

.lead {
  margin-bottom: @line-height-computed;
  font-size: floor((@font-size-base * 1.15));
  font-weight: 300;
  line-height: 1.7;

  @media (min-width: @screen-sm-min) {
    font-size: (@font-size-base * 1.5);
  }
}


// Emphasis & misc
// -------------------------

// Ex: 14px base font * 85% = about 12px
small,
.small  { font-size: 85%; }

// Undo browser default styling
cite    { font-style: normal; }

// Alignment
.text-left           { text-align: left; }
.text-right          { text-align: right; }
.text-center         { text-align: center; }
.text-lowercase      { text-transform: lowercase; }
.text-uppercase      { text-transform: uppercase; }
.text-capitalize     { text-transform: capitalize; }
.text-justify        { text-align: justify; }

// Contextual colors
.text-muted {
  color: @text-muted;
}
.text-primary {
  .text-emphasis-variant(@brand-primary);
}
.text-success {
  .text-emphasis-variant(@state-success-text);
}
.text-info {
  .text-emphasis-variant(@state-info-text);
}
.text-warning {
  .text-emphasis-variant(@state-warning-text);
}
.text-danger {
  .text-emphasis-variant(@state-danger-text);
}
.text-orange {
  .text-emphasis-variant(@theme-orange); 
}
.text-red {
  .text-emphasis-variant(@theme-red); 
}
.text-pink {
  .text-emphasis-variant(@theme-pink); 
}
.text-yellow {
  .text-emphasis-variant(@theme-yellow); 
}
.text-blue {
  .text-emphasis-variant(@theme-blue); 
}
.text-sky {
  .text-emphasis-variant(@theme-sky); 
}
.text-green {
  .text-emphasis-variant(@theme-green); 
}
.text-violet {
  .text-emphasis-variant(@theme-violet); 
}

// Contextual backgrounds
// For now we'll leave these alongside the text classes until v4 when we can
// safely shift things around (per SemVer rules).
.bg-primary {
  // Given the contrast here, this is the only class to have its color inverted
  // automatically.
  color: #fff;
  .bg-variant(@brand-primary);
}
.bg-success {
  .bg-variant(@state-success-bg);
}
.bg-info {
  .bg-variant(@state-info-bg);
}
.bg-warning {
  .bg-variant(@state-warning-bg);
}
.bg-danger {
  .bg-variant(@state-danger-bg);
}

// Kanda custom Background
.bg-default{
  border: 2px solid darken(@btn-default-bg, 10%);
  .bg-variant(darken(@btn-default-bg, 5%));
}
.bg-inverse{
  color: #ffffff;
  .bg-variant(@btn-inverse-bg);
}
.bg-orange{
  color: #ffffff;
  .bg-variant(@btn-orange-bg);
}
.bg-red{
  color: #ffffff;
  .bg-variant(@btn-red-bg);
}
.bg-pink{
  color: #ffffff;
  .bg-variant(@btn-pink-bg);
}
.bg-yellow{
  color: #ffffff;
  .bg-variant(@btn-yellow-bg);
}
.bg-blue{
  color: #ffffff;
  .bg-variant(@btn-blue-bg);
}
.bg-sky{
  color: #ffffff;
  .bg-variant(@btn-sky-bg);
}
.bg-green{
  color: #ffffff;
  .bg-variant(@btn-green-bg);
}
.bg-violet{
  color: #ffffff;
  .bg-variant(@btn-violet-bg);
}

// Page header
// -------------------------

.page-header {
  padding-bottom: @line-height-computed - 3;
  margin: (@line-height-computed * 2) 0 @line-height-computed;
  border-bottom: 2px solid @page-header-border-color;
}

// Kanda minor header
// ------------------------

.minor-header{
  font-size: 90%;
  padding-bottom: @line-height-computed - 15;
  margin: (@line-height-computed * 2) 0 @line-height-computed;
  border-bottom: 2px solid @page-header-border-color;
}


// Lists
// --------------------------------------------------

// Unordered and Ordered lists
ul,
ol {
  margin-top: 0;
  margin-bottom: (@line-height-computed / 2);
  ul,
  ol {
    margin-bottom: 0;
  }
}

// List options

// Unstyled keeps list items block level, just removes default browser padding and list-style
.list-unstyled {
  padding-left: 0;
  list-style: none;
}

// Inline turns list items into inline-block
.list-inline {
  .list-unstyled();
  margin-left: -5px;

  > li {
    display: inline-block;
    padding-left: 5px;
    padding-right: 5px;
  }
}

// Description Lists
dl {
  margin-top: 0; // Remove browser default
  margin-bottom: @line-height-computed;
  // Description Lists with Checkmark icon
  &.dl-check{
    dt{
      line-height: (@line-height-computed + 5);
      &:before{
        content: "\221A";
        font-weight: bold;
        margin-right: 10px;
        position: relative;
        top: 1px;
      }
    }
  }
  &.dl-arrow{
    dt{
      line-height: (@line-height-computed + 5);
      &:before{
        content: "\2192";
        font-weight: bold;
        margin-right: 5px;
        position: relative;
        top: 1px;
      }
    }
  }
}
dt,
dd {
  line-height: @line-height-base;
}
dt {
  font-weight: bold;
}
dd {
  margin-left: 0; // Undo browser default
}

// Horizontal description lists
//
// Defaults to being stacked without any of the below styles applied, until the
// grid breakpoint is reached (default of ~768px).

.dl-horizontal {
  dd {
    &:extend(.clearfix all); // Clear the floated `dt` if an empty `dd` is present
  }

  @media (min-width: @grid-float-breakpoint) {
    dt {
      float: left;
      width: (@dl-horizontal-offset - 20);
      clear: left;
      text-align: right;
      .text-overflow();
    }
    dd {
      margin-left: @dl-horizontal-offset;
    }
  }
}

// MISC
// ----

// Abbreviations and acronyms
abbr[title],
// Add data-* attribute to help out our tooltip plugin, per https://github.com/twbs/bootstrap/issues/5257
abbr[data-original-title] {
  cursor: help;
  border-bottom: 1px dotted @abbr-border-color;
}
.initialism {
  font-size: 90%;
  text-transform: uppercase;
}

// Blockquotes
blockquote {
  padding: (@line-height-computed / 2) @line-height-computed;
  margin: 0 0 @line-height-computed;
  font-size: @blockquote-font-size;
  border-left: 5px solid @blockquote-border-color;

  p,
  ul,
  ol {
    &:last-child {
      margin-bottom: 0;
    }
  }

  // Note: Deprecated small and .small as of v3.1.0
  // Context: https://github.com/twbs/bootstrap/issues/11660
  footer,
  small,
  .small {
    display: block;
    font-size: 80%; // back to default font-size
    line-height: @line-height-base;
    color: @blockquote-small-color;

    &:before {
      content: '\2014 \00A0'; // em dash, nbsp
    }
  }
}

// Opposite alignment of blockquote
//
// Heads up: `blockquote.pull-right` has been deprecated as of v3.1.0.
.blockquote-reverse,
blockquote.pull-right {
  padding-right: 15px;
  padding-left: 0;
  border-right: 5px solid @blockquote-border-color;
  border-left: 0;
  text-align: right;

  // Account for citation
  footer,
  small,
  .small {
    &:before { content: ''; }
    &:after {
      content: '\00A0 \2014'; // nbsp, em dash
    }
  }
}

// Quotes
blockquote:before,
blockquote:after {
  content: "";
}

// Addresses
address {
  margin-bottom: @line-height-computed;
  font-style: normal;
  line-height: @line-height-base;
}

// Blockquotes-icon
.blockquote-icon {
  position: relative;
  padding: (@line-height-computed / 2) @line-height-computed @line-height-computed (@line-height-computed * 2) ;
  margin: 0 0 @line-height-computed;
  font-size: @blockquote-font-size;
  border: 0;

  &:before{
    content: '\201C';
    font-size: (@font-size-base * 3);
    position: absolute;
    left: 10px;
    top: 0;
    margin: 0;
    padding: 0;
    line-height: 1em;
    color: #aaa;
  }

  &:after{
    content: '\201D';
    font-size: (@font-size-base * 3);
    position: absolute;
    right: 10px;
    bottom: 0;
    margin: 0;
    padding: 0;
    line-height: 0.5em;
    color: #aaa;
  }

  p,
  ul,
  ol {
    &:last-child {
      margin-bottom: 0;
    }
  }

  // Note: Deprecated small and .small as of v3.1.0
  // Context: https://github.com/twbs/bootstrap/issues/11660
  footer,
  small,
  .small {
    display: block;
    font-size: 80%; // back to default font-size
    line-height: @line-height-base;
    color: @blockquote-small-color;

    &:before {
      content: '\2014 \00A0'; // em dash, nbsp
    }
  }
}

// Blockquotes-icon-left
.blockquote-icon-left {
  position: relative;
  padding: (@line-height-computed / 2) @line-height-computed @line-height-computed (@line-height-computed * 2) ;
  margin: 0 0 @line-height-computed;
  font-size: @blockquote-font-size;
  border: 0;

  &:before{
    content: '\201C';
    font-size: (@font-size-base * 3);
    position: absolute;
    left: 10px;
    top: 0;
    margin: 0;
    padding: 0;
    line-height: 1em;
    color: #aaa;
  }

  &:after{
    content: '';
  }

  p,
  ul,
  ol {
    &:last-child {
      margin-bottom: 0;
    }
  }

  // Note: Deprecated small and .small as of v3.1.0
  // Context: https://github.com/twbs/bootstrap/issues/11660
  footer,
  small,
  .small {
    display: block;
    font-size: 80%; // back to default font-size
    line-height: @line-height-base;
    color: @blockquote-small-color;

    &:before {
      content: '\2014 \00A0'; // em dash, nbsp
    }
  }
}
