//
// Labels
// --------------------------------------------------

.label {
  display: inline;
  padding: .15em .6em .2em;
  font-size: 75%;
  font-weight: normal;
  line-height: 1;
  color: @label-color;
  text-align: center;
  white-space: nowrap;
  vertical-align: baseline;
  position: relative;
  top: -1px;
  text-shadow: 0px 2px 1px rgba(0, 0, 0, 0.2);
  -webkit-border-radius: 2px;
  -moz-border-radius: 2px;
  border-radius: 2px;

  // Add hover effects, but only for links
  &[href] {
    &:hover,
    &:focus {
      color: @label-link-hover-color;
      text-decoration: none;
      cursor: pointer;
    }
  }

  // Empty labels collapse automatically (not available in IE8)
  &:empty {
    display: none;
  }

  // Quick fix for labels in buttons
  .btn & {
    position: relative;
    top: -1px;
  }
}

// Colors
// Contextual variations (linked labels get darker on :hover)

.label-default {
  .label-variant(@label-default-bg);
}

.label-primary {
  .label-variant(@label-primary-bg);
}

.label-success {
  .label-variant(@label-success-bg);
}

.label-info {
  .label-variant(@label-info-bg);
}

.label-warning {
  .label-variant(@label-warning-bg);
}

.label-danger {
  .label-variant(@label-danger-bg);
}

// KANDA - CUSTOM COLORS
.label{
  &.orange{
    .label-variant(@label-orange-bg);
  }
  &.red{
    .label-variant(@label-red-bg);
  }
  &.pink{
    .label-variant(@label-pink-bg);
  }
  &.yellow{
    .label-variant(@label-yellow-bg);
  }
  &.blue{
    .label-variant(@label-blue-bg);
  }
  &.sky{
    .label-variant(@label-sky-bg);
  }
  &.green{
    .label-variant(@label-green-bg);
  }
  &.violet{
    .label-variant(@label-violet-bg);
  }
  &.inverse{
    .label-variant(@label-inverse-bg);
  }
}


