//
// Navs
// --------------------------------------------------


// Base class
// --------------------------------------------------

.nav {
  margin-bottom: 0;
  padding-left: 0; // Override default ul/ol
  list-style: none;
  &:extend(.clearfix all);

  > li {
    position: relative;
    display: block;

    > a {
      position: relative;
      display: block;
      padding: @nav-link-padding;
      &:hover,
      &:focus {
        text-decoration: none;
        background-color: @nav-link-hover-bg;
      }
    }

    // Disabled state sets text to gray and nukes hover/tab effects
    &.disabled > a {
      color: @nav-disabled-link-color;

      &:hover,
      &:focus {
        color: @nav-disabled-link-hover-color;
        text-decoration: none;
        background-color: transparent;
        cursor: not-allowed;
      }
    }
  }

  // Open dropdowns
  .open > a {
    &,
    &:hover,
    &:focus {
      background-color: @nav-link-hover-bg;
      // border-color: @link-color;
    }
  }

  // Nav dividers (deprecated with v3.0.1)
  //
  // This should have been removed in v3 with the dropping of `.nav-list`, but
  // we missed it. We don't currently support this anywhere, but in the interest
  // of maintaining backward compatibility in case you use it, it's deprecated.
  .nav-divider {
    .nav-divider();
  }

  // Prevent IE8 from misplacing imgs
  //
  // See https://github.com/h5bp/html5-boilerplate/issues/984#issuecomment-3985989
  > li > a > img {
    max-width: none;
  }
}


// Tabs
// -------------------------

// Give the tabs something to sit on
.nav-tabs {
  border-bottom: 2px solid @nav-tabs-border-color;
  > li {
    float: left;
    // Make the list-items overlay the bottom border
    margin-bottom: -3px;

    // Actual tabs (as links)
    > a {
      margin-right: 0px;
      line-height: @line-height-base;
      border: 1px solid transparent;
      border-radius: @border-radius-base @border-radius-base 0 0;
      &:hover {
        border-color: transparent;
      }
    }

    // Active state, and its :hover to override normal :hover
    &.active > a {
      &,
      &:hover,
      &:focus {
        color: @nav-tabs-active-link-hover-color;
        background-color: @nav-tabs-active-link-hover-bg;
        border: 2px solid @nav-tabs-active-link-hover-border-color;
        border-bottom-color: transparent;
        cursor: default;
      }
    }
  }
  // pulling this in mainly for less shorthand
  &.nav-justified {
    .nav-justified();
    .nav-tabs-justified();
  }
  // Kanda with color palatte
  &.orange{
    border-bottom: 2px solid @theme-orange;
    > li {
      margin-bottom: 0px;
      > a {
        color: @theme-orange;
        border: 0px;
      }
      &.active > a {
        &,
        &:hover,
        &:focus {
          color: @component-active-color;
          background-color: @theme-orange;
          border: 0px solid @nav-tabs-active-link-hover-border-color;
        }
      }
    }
  }
  // inverse
  &.inverse{
    border-bottom: 2px solid @theme-inverse;
    > li {
      margin-bottom: 0px;
      > a {
        .transition(all 0.1s ease-in-out);
        color: @theme-inverse;
        border: 0px;
      }
      &.active > a {
        &,
        &:hover,
        &:focus {
          .transition(all 0.1s ease-in-out);
          color: @component-active-color;
          background-color: @theme-inverse;
          border: 0px solid @nav-tabs-active-link-hover-border-color;
        }
      }
    }
  }
  // red
  &.red{
    border-bottom: 2px solid @theme-red;
    > li {
      margin-bottom: 0px;
      > a {
        color: @theme-red;
        border: 0px;
      }
      &.active > a {
