    > li {
      margin-bottom: 0px;
      > a {
        color: @theme-yellow;
        border: 0px;
      }
      &.active > a {
        &,
        &:hover,
        &:focus {
          color: @component-active-color;
          background-color: darken(@theme-yellow, 10%);
          border: 0px solid @nav-tabs-active-link-hover-border-color;
        }
      }
    }
  }
  // blue
  &.blue{
    border-bottom: 2px solid @theme-blue;
    > li {
      margin-bottom: 0px;
      > a {
        color: @theme-blue;
        border: 0px;
      }
      &.active > a {
        &,
        &:hover,
        &:focus {
          color: @component-active-color;
          background-color: @theme-blue;
          border: 0px solid @nav-tabs-active-link-hover-border-color;
        }
