// Stacked pills
.nav-stacked {
  > li {
    float: none;
    + li {
      margin-top: 2px;
      margin-left: 0; // no need for this gap between nav items
    }
  }
}


// Nav variations
// --------------------------------------------------

// Justified nav links
// -------------------------

.nav-justified {
  width: 100%;

  > li {
    float: none;
    > a {
      text-align: center;
      margin-bottom: 5px;
    }
  }

  > .dropdown .dropdown-menu {
    top: auto;
    left: auto;
    @media (max-width: @screen-xs-max) {
      width: 100%;
    }
  }

  @media (min-width: @screen-sm-min) {
    > li {
      display: table-cell;
      width: 1%;
      > a {
        margin-bottom: 0;
      }
    }
  }
}

// Move borders to anchors instead of bottom of list
//
// Mixin for adding on top the shared `.nav-justified` styles for our tabs
.nav-tabs-justified {
  border-bottom: 0;

  > li > a {
    // Override margin from .nav-tabs
    margin-right: 0;
    border-radius: @border-radius-base;
  }

  > .active > a,
  > .active > a:hover,
  > .active > a:focus {
    border: 2px solid @nav-tabs-justified-link-border-color;
  }

  @media (min-width: @screen-sm-min) {
    > li > a {
      border-bottom: 2px solid @nav-tabs-justified-link-border-color;
      border-radius: @border-radius-base @border-radius-base 0 0;
    }
    > .active > a,
    > .active > a:hover,
    > .active > a:focus {
      border-bottom-color: @nav-tabs-justified-active-link-border-color;
    }
  }
}

// Tabbable tabs
// -------------------------

// Hide tabbable panes to start, show them when `.active`
.tab-content {
  margin-top: 10px;
  > .tab-pane {
    display: none;
  }
  > .active {
    display: block;
  }
}


// Dropdowns
// -------------------------

// Specific dropdowns
.nav-tabs .dropdown-menu {
  // make dropdown border overlap tab border
  margin-top: -2px;
  // Remove the top rounded corners here since there is a hard edge above the menu
  .border-top-radius(0);
}

.nav-pills .dropdown-menu {
  // make dropdown border overlap tab border
  margin-top: -1px;
  // Remove the top rounded corners here since there is a hard edge above the menu
  .border-top-radius(0);
}


// KANDA - DIRECTIONS
// -------------------------
.tabs-bottom{
  .nav-tabs{
    border-bottom: 0;
    > li{
      + li{
        margin-left: -2px;
      }
      a{
        border-bottom: 0;
      }
      // active state
      &.active > a {
        &,
        &:hover,
        &:focus {
          color: @nav-tabs-active-link-hover-color;
          background-color: @nav-tabs-active-link-hover-bg;
          border: 2px solid @nav-tabs-active-link-hover-border-color;
          border-top-color: transparent;
          cursor: default;
        }
      }
    }
    // KANDA - Custom Colors
    &.orange{ .nav-bottom-variant(@theme-orange); }
    &.red{ .nav-bottom-variant(@theme-red); }
    &.pink{ .nav-bottom-variant(@theme-pink); }
    &.yellow{ .nav-bottom-variant(@theme-yellow); }
    &.blue{ .nav-bottom-variant(@theme-blue); }
    &.sky{ .nav-bottom-variant(@theme-sky); }
    &.green{ .nav-bottom-variant(@theme-green); }
    &.violet{ .nav-bottom-variant(@theme-violet); }
    &.inverse{ .nav-bottom-variant(@theme-inverse); }
  }
  .tab-content{
    margin-top: 0;
    margin-bottom: 10px;
  }
}

