//
// Badges
// --------------------------------------------------


// Base classes
.badge {
  border-bottom: 2px solid darken(@badge-bg, 3%);
  border-right: 2px solid darken(@badge-bg, 3%);
  display: inline-block;
  margin-left: 3px;
  min-width: 10px;
  padding: 4px 6px 3px 7px;
  font-size: @font-size-ex-small;
  font-weight: @badge-font-weight;
  color: @badge-color;
  line-height: @badge-line-height;
  vertical-align: baseline;
  white-space: nowrap;
  text-align: center;
  background-color: @badge-bg;
  border-radius: @badge-border-radius;

  // Empty badges collapse automatically (not available in IE8)
  &:empty {
    display: none;
  }

  // Quick fix for badges in buttons
  .btn & {
    position: relative;
    top: -1px;
    border-color: #e6e6e6;
  }
  .btn-xs & {
    top: 0;
    padding: 1px 5px;
  }
  // KANDA CUSTOM COLORS
  &.orange{
    .badge-variant(@label-orange-bg);
  }
  &.red{
    .badge-variant(@label-red-bg);
  }
  &.pink{
    .badge-variant(@label-pink-bg);
  }
  &.yellow{
    .badge-variant(@label-yellow-bg);
  }
  &.blue{
    .badge-variant(@label-blue-bg);
  }
  &.sky{
    .badge-variant(@label-sky-bg);
  }
  &.green{
    .badge-variant(@label-green-bg);
  }
  &.violet{
    .badge-variant(@label-violet-bg);
  }
  &.inverse{
    .badge-variant(@label-inverse-bg);
  }
}

// Hover state, but only for links
a.badge {
  &:hover,
  &:focus {
    color: @badge-link-hover-color;
    text-decoration: none;
    cursor: pointer;
  }
}

// Account for counters in navs
a.list-group-item.active > .badge,
.nav-pills > .active > a > .badge {
  color: @badge-active-color;
  background-color: @badge-active-bg;
  border-color: #e6e6e6;
}
.nav-pills > li > a > .badge {
  margin-left: 3px;
  position: relative;
  top: -1px;
}
a{
  .badge{
    position: relative;
    top: -1px;
  }
}
.nav-stacked{
  .badge{
    top: 2px !important;
  }
}
