//
// Progress bars
// --------------------------------------------------


// Bar animations
// -------------------------

// WebKit
@-webkit-keyframes progress-bar-stripes {
  from  { background-position: 40px 0; }
  to    { background-position: 0 0; }
}

// Spec and IE10+
@keyframes progress-bar-stripes {
  from  { background-position: 40px 0; }
  to    { background-position: 0 0; }
}



// Bar itself
// -------------------------

// Outer container
.progress {
  overflow: hidden;
  height: (@line-height-computed - 3);
  margin-bottom: (@line-height-computed - 3);
  background-color: @progress-bg;
  border-radius: @border-radius-base;
  .box-shadow(inset 0 1px 2px rgba(0,0,0,0.1));
}

// Bar of progress
.progress-bar {
  float: left;
  width: 0%;
  height: 100%;
  font-size: @font-size-ex-small;
  line-height: (@line-height-computed - 3);
  color: @progress-bar-color;
  text-align: center;
  background-color: @progress-bar-bg;
  border-radius: 0 2px 2px 0;
  .box-shadow(inset 0 -2px 0 0 rgba(0,0,0,.15));
  .transition(width .6s ease);
  + .progress-bar{
    border-radius: 0;
  }
  &.first{
    border-radius: 0;
  }
}

// Striped bars
//
// `.progress-striped .progress-bar` is deprecated as of v3.2.0 in favor of the
// `.progress-bar-striped` class, which you just add to an existing
// `.progress-bar`.
.progress-striped .progress-bar,
.progress-bar-striped {
  #gradient > .striped();
  background-size: 40px 40px;
}

.progress.active .progress-bar,
.progress-bar.active {
  .animation(progress-bar-stripes 1s linear infinite);
}

// Account for lower percentages
.progress-bar {
  &[aria-valuenow="1"],
  &[aria-valuenow="2"] {
    min-width: 30px;
  }

  &[aria-valuenow="0"] {
    color: @gray-light;
    min-width: 30px;
    background-color: transparent;
    background-image: none;
    box-shadow: none;
  }
}

// Variations
// -------------------------

.progress-bar-success {
  .progress-bar-variant(@progress-bar-success-bg);
}

.progress-bar-info {
  .progress-bar-variant(@progress-bar-info-bg);
}

.progress-bar-warning {
  .progress-bar-variant(@progress-bar-warning-bg);
}

.progress-bar-danger {
  .progress-bar-variant(@progress-bar-danger-bg);
}

// KANDA CUSTOM COLORS
// ------------------------

.progress-bar{
  &.orange{ .progress-bar-variant(@progress-bar-orange-bg); }
  &.red{ .progress-bar-variant(@progress-bar-red-bg); }
  &.pink{ .progress-bar-variant(@progress-bar-pink-bg); }
  &.yellow{ .progress-bar-variant(@progress-bar-yellow-bg); }
  &.blue{ .progress-bar-variant(@progress-bar-blue-bg); }
  &.sky{ .progress-bar-variant(@progress-bar-sky-bg); }
  &.green{ .progress-bar-variant(@progress-bar-green-bg); }
  &.violet{ .progress-bar-variant(@progress-bar-violet-bg); }
  &.inverse{ .progress-bar-variant(@progress-bar-inverse-bg); }
}
