//
// Modals
// --------------------------------------------------

// .modal-open      - body class for killing the scroll
// .modal           - container to scroll within
// .modal-dialog    - positioning shell for the actual modal
// .modal-content   - actual modal w/ bg and corners and shit

// Kill the scroll on the body
.modal-open {
  overflow: hidden;
}

// Container that the modal scrolls within
.modal {
  display: none;
  overflow: auto;
  overflow-y: scroll;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: @zindex-modal;
  -webkit-overflow-scrolling: touch;

  // Prevent Chrome on Windows from adding a focus outline. For details, see
  // https://github.com/twbs/bootstrap/pull/10951.
  outline: 0;

  // When fading in the modal, animate it to slide down
  &.fade {
    .modal-dialog {
      .translate(0, -25%);
      .transition-transform(~"0.3s ease-out");
    }
    &.in {
      .modal-dialog { .translate(0, 0)}
    }
  }

  // KANDA Animate effects
  &.scale-effect{
    .modal-dialog{
      -webkit-transform: scale(0.8);
      -moz-transform: scale(0.8);
      -o-transform: scale(0.8);
      -ms-transform: scale(0.8);
      transform: scale(0.8);
      .transition-transform(~"0.3s ease-out");
    }
    &.in {
      .modal-dialog { 
        -webkit-transform: scale(1);
        -moz-transform: scale(1);
        -o-transform: scale(1);
        -ms-transform: scale(1);
        transform: scale(1);
      };
    }
  } 
  // super scale
  &.super-effect{
    .modal-dialog{
      -webkit-transform: scale3d(1.3, 1.3, 1.3);
      -moz-transform: scale3d(1.3, 1.3, 1.3);
      -o-transform: scale3d(1.3, 1.3, 1.3);
      -ms-transform: scale3d(1.3, 1.3, 1.3);
      transform: scale3d(1.3, 1.3, 1.3);
      .transition-transform(~"0.2s ease-out");
    }
    &.in {
      .modal-dialog { 
        -webkit-transform: scale3d(1, 1, 1);
        -moz-transform: scale3d(1, 1, 1);
        -o-transform: scale3d(1, 1, 1);
        -ms-transform: scale3d(1, 1, 1);
        transform: scale3d(1, 1, 1);
      };
    }
  }
  // bottom in
  &.bottomin-effect{
    .modal-dialog{
      .translate(0, 10%);
      .transition-transform(~"0.3s ease-out");
    }
    &.in {
      .modal-dialog { 
        .translate(0, 0%);
      };
    }
  }
  // flip
  &.flip-effect{
    .modal-dialog{
      -webkit-transform: perspective(900px) rotateY(-30deg);
      -moz-transform: perspective(900px) rotateY(-30deg);
      -o-transform: perspective(900px) rotateY(-30deg);
      -ms-transform: perspective(900px) rotateY(-30deg);
      transform: perspective(900px) rotateY(-30deg);
      .transition-transform(~"0.3s ease-out");
    }
    &.in {
      .modal-dialog { 
        -webkit-transform: perspective(900px) rotateY(0deg);
        -moz-transform: perspective(900px) rotateY(0deg);
        -o-transform: perspective(900px) rotateY(0deg);
        -ms-transform: perspective(900px) rotateY(0deg);
        transform: perspective(900px) rotateY(0deg);
      };
    }
  }
  // flip-vertical
  &.flip-vertical-effect{
    .modal-dialog{
      -webkit-transform: perspective(900px) rotateX(-30deg);
      -moz-transform: perspective(900px) rotateX(-30deg);
      -o-transform: perspective(900px) rotateX(-30deg);
      -ms-transform: perspective(900px) rotateX(-30deg);
      transform: perspective(900px) rotateX(-30deg);
      .transition-transform(~"0.3s ease-out");
    }
    &.in {
      .modal-dialog { 
        -webkit-transform: perspective(900px) rotateX(0deg);
        -moz-transform: perspective(900px) rotateX(0deg);
        -o-transform: perspective(900px) rotateX(0deg);
        -ms-transform: perspective(900px) rotateX(0deg);
        transform: perspective(900px) rotateX(0deg);
      };
    }
  }
}

// Shell div to position the modal with bottom padding
.modal-dialog {
  position: relative;
  width: auto;
  margin: 10px;
}

// Actual modal
.modal-content {
  position: relative;
  background-color: @modal-content-bg;
  border: 2px solid @modal-content-fallback-border-color; //old browsers fallback (ie8 etc)
  border-radius: @border-radius-large;
  // .box-shadow(0 3px 9px rgba(0,0,0,.5));
  .box-shadow(0 5px 7px -5px rgba(0, 0, 0, 1));
  background-clip: padding-box;
  // Remove focus outline from opened modal
  outline: none;
}

// Modal background
.modal-backdrop {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: @zindex-modal-background;
  background-color: @modal-backdrop-bg;
  // Fade for backdrop
  &.fade { .opacity(0); }
  &.in { .opacity(@modal-backdrop-opacity); }
}

// Modal header
// Top section of the modal w/ title and dismiss
.modal-header {
  padding: @modal-title-padding;
  border-bottom: 1px solid @modal-header-border-color;
  min-height: (@modal-title-padding + @modal-title-line-height);
  background-color: @popover-bg;
  color: #ffffff;
}
// Close icon
.modal-header .close {
  margin-top: -2px;
  color: rgba(255,255,255,0.5);
}

// Title text within header
.modal-title {
  margin: 0;
  line-height: @modal-title-line-height;
}

// Modal body
// Where all modal content resides (sibling of .modal-header and .modal-footer)
.modal-body {
  position: relative;
  padding: @modal-inner-padding;
}

// Footer (for actions)
.modal-footer {
  padding: (@modal-inner-padding - 1) @modal-inner-padding @modal-inner-padding;
  text-align: right; // right align buttons
  border-top: 1px solid @modal-footer-border-color;
  &:extend(.clearfix all); // clear it in case folks use .pull-* classes on buttons

  // Properly space out buttons
  .btn + .btn {
    margin-left: 5px;
    margin-bottom: 0; // account for input[type="submit"] which gets the bottom margin like all other inputs
  }
  // but override that for button groups
  .btn-group .btn + .btn {
    margin-left: -1px;
  }
  // and override it for block buttons as well
  .btn-block + .btn-block {
    margin-left: 0;
  }
}

// Scale up the modal
@media (min-width: @screen-sm-min) {
  // Automatically set modal's width for larger viewports
  .modal-dialog {
    width: @modal-md;
    margin: 30px auto;
  }
  .modal-content {
    .box-shadow(0 5px 7px -5px rgba(0, 0, 0, 1));
  }

  // Modal sizes
  .modal-sm { width: @modal-sm; }
}

@media (min-width: @screen-md-min) {
  .modal-lg { width: @modal-lg; }
}
