//
// Utility classes
// --------------------------------------------------


// Floats
// -------------------------

.clearfix {
  .clearfix();
}
.center-block {
  .center-block();
}
.pull-right {
  float: right !important;
}
.pull-left {
  float: left !important;
}


// Toggling content
// -------------------------

// Note: Deprecated .hide in favor of .hidden or .sr-only (as appropriate) in v3.0.1
.hide {
  display: none !important;
}
.show {
  display: block !important;
}
.invisible {
  visibility: hidden;
}
.text-hide {
  .text-hide();
}

.img-lightbox{
  cursor: -webkit-zoom-in;
  cursor: -moz-zoom-in;
}

// Hide from screenreaders and browsers
//
// Credit: HTML5 Boilerplate

.hidden {
  display: none !important;
  visibility: hidden !important;
}


// For Affix plugin
// ------------------------------------

.affix {
  position: fixed;
}

// Responsive Videos
// REMOVE ON 2.0
// ------------------------------------

// Fixed, Columns margin [XtraSmall devices]
// ------------------------------------
.with-margin-xs{
  @media (max-width: @screen-xs-max) {
    margin-top: 20px;
  }
}

// Bootstrap WYSIWYG
// ------------------------------------

.text-editor {
  max-height: 200px;
  height: 200px;
  background-color: #fcfcfc;
  border: 2px solid #e6e6e6;
  padding: 10px;
  border-collapse: separate;
  box-sizing: border-box;
  -webkit-box-shadow: rgba(0, 0, 0, 0.0745098) 0px 1px 1px 0px inset;
  box-shadow: rgba(0, 0, 0, 0.0745098) 0px 1px 1px 0px inset;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  border-radius: 3px;
  overflow: scroll;
  outline: none;
}

// Masonry items
// ------------------------------------

.masonry-item{
  float: left;
  max-width: 300px;
  margin: 5px;
  &.col1{ width: 140px }
  &.col2{ width: 200px }
  &.col3{ width: 250px }
  img{
    max-width: 100%;
    height: auto;
    display: block;
  }
}
